  <AuthModal v-if="isAuthModalVisible" @close="closeAuthModal" />
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { useAuthStore } from '@/store/authStore';
import AuthModal from '@/components/AuthModal.vue';

const authStore = useAuthStore();
const isAuthModalVisible = ref(false);

// Получаем состояние авторизации и email пользователя
const isAuthenticated = computed(() => authStore.isAuthenticated);
const userEmail = computed(() => authStore.userEmail);

const isMobile = ref(window.innerWidth <= 360);
const showTooltip = ref(false);

const showAuthModal = () => {
  isAuthModalVisible.value = true;
};

const closeAuthModal = () => {
  isAuthModalVisible.value = false;
};

const logout = async () => {
  await authStore.logout();
};

const handleResize = () => {
  isMobile.value = window.innerWidth <= 360;
};

onMounted(() => {
  window.addEventListener('resize', handleResize);
});

onUnmounted(() => {
  window.removeEventListener('resize', handleResize);
});
</script>

<style scoped>
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 43px 0;
}

.tooltip {
  position: absolute;
  bottom: -250%;
  left: 65%;
  transform: translateX(-50%);
  background-color: var(--dark-middle-color);
  color: var(--green-light-color);
  padding: 40px;
  font-size: 18px;
  font-weight: 700;
  border-radius: 12px;
  z-index: 10;
  transition: opacity 0.2s;
  box-shadow: 0px 15px 46px -10px rgba(0, 0, 0, 0.6);
}

.tooltip:after {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 60%;
  margin-left: 0;
  border-width: 9px;
  border-style: solid;
  border-color: transparent transparent var(--dark-middle-color) transparent;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
  position: relative;
}

.user-email {
  color: var(--text-white-color);
  font-size: 18px;
}

.button-exit {
  background-color: var(--dark-middle-color);
  padding: 14px 18px;
}

button {
  display: inline-flex;
  align-items: center;
  gap: 17px;
}

/* Адаптивные стили */
@media (max-width: 1367px) {
  nav {
    padding: 23px 0;
  }
}

@media (max-width: 769px) {
  nav {
    padding: 23px 0;
  }
}

@media (max-width: 361px) {
  nav {
    padding: 20px 0;
  }

  .user-email {
    font-size: 14px;
  }

  .button-exit {
    padding: 9px 11px;
  }
}
</style>